body {
  margin: 0;
  padding: 0;
}

#book-search, #results, #header-section, #saved-books {
  border: 1px solid #ececec;
  border-radius: 3px;
  padding: 20px 20px 40px 20px;
  margin: 40px 0px;
  font-family: 'Roboto', sans-serif;
}

.section-titles {
  padding-bottom: 20px;
  font-family: 'Poppins', sans-serif;
  color: #4885ed;
}

.google-blue {
  color: #4885ed;
}

.google-red {
  color: #db3236;
}

.google-yellow {
  color: #f4c20d;
}

.google-green {
  color: #3cba54;
}
.navbar {
    background: #4885ed;
}

.navbar-brand {
    font-size: 1.5em !important;
}

.navbar-brand, .nav-link {
    color: white;
    border: 1px solid #4885ed;
}

.navbar-brand:hover {
    color: white;
}

.nav-link:hover {
    border: 1px solid #eee;
    border-radius: 3px;
    box-shadow: 0px 1px 2px #2968d4;
    color: white;
} 

#savedNum {
    color: #4885ed;
    background: white;
    border-radius: 3px;
    padding: 1px 3px;
}
#header-section {
    margin-top: 120px;
}

h1, h4 {
    font-family: 'Poppins', sans-serif;
}

h4 {
    color: #444;
}

.searchBox {
    border-top-right-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
    color: #444;
    background: white;
}

.searchBox:focus {
    border: none !important;
    outline: none !important;
    box-shadow: 1px 2px 4px #999 !important;
    background: white !important
}

.searchBtn {
    border-top-left-radius: 0px !important;
    border-bottom-left-radius: 0px !important;
}

.searchBtn:hover {
    border: 1px solid #555 !important;
    box-shadow: 1px 2px 4px #999 !important;
}
.card {
    color: #444;
}

.card-title, .card-text {
    font-family: 'Roboto', sans-serif;
}

.card-title {
    font-size: 1.5em;
    font-family: 'Poppins', sans-serif;
}

.btnWrapper {
    position: absolute;
    right: 0;
}

@media (max-width: 768px) {
    .btnWrapper {
        position: relative;
        margin-bottom: 20px;
        display: flex;
        justify-content: center;
    }

    .saveBtns, .viewBtns, .deleteBtns {
        width: 50%;
    }
}

.saveBtns {
    background: #3cba54 !important;
    border: 1px solid #3cba54 !important;
}

.viewBtns {
    background: #4885ed !important;
    border: 1px solid #4885ed !important;
    color: white !important;
}

.deleteBtns {
    background: #db3236 !important;
    border: 1px solid #db3236 !important;
}

.saveBtns:hover, .viewBtns:hover, .deleteBtns:hover {
    box-shadow: 1px 2px 4px #999;
} 
